<template>
  <n-card class="user-list-card" content-style="padding: 0;">
    <div class="user-list-title">联系人</div>
    <n-list hoverable clickable>
      <n-list-item v-for="user in users" :key="user.id" @click="$emit('selectUser', user)">
        <template #prefix>
          <n-avatar :size="32" round>{{ user.chatName?.charAt(0) }}</n-avatar>
        </template>
        <div :class="['user-list-name', {selected: user.id === selectedUser.id}]">{{ user.username }}</div>
      </n-list-item>
    </n-list>
  </n-card>
</template>

<script>
export default {
  name: 'UserList',
  props: {
    users: Array,
    selectedUser: Object
  }
}
</script>

<style scoped>
.user-list-card {
  border-radius: 0;
  height: 100vh;
  box-shadow: none;
  border-right: 1px solid #e0eaff;
  padding: 0;
}
.user-list-title {
  font-weight: bold;
  font-size: 18px;
  padding: 20px 0 10px 24px;
  background: #f7f9fb;
  border-bottom: 1px solid #e0eaff;
}
.user-list-name {
  font-size: 16px;
  padding-left: 8px;
  transition: color 0.2s;
}
.user-list-name.selected {
  color: #18a058;
  font-weight: bold;
}
</style> 