<template>
  <div class="chat-container">
    <n-layout has-sider position="absolute" style="height: 100vh">
      <n-layout-sider width="260" bordered content-style="padding: 0;">
        <UserList :users="users" :selectedUser="selectedUser" @selectUser="selectUser" />
      </n-layout-sider>
      <n-layout>
        <n-card class="chat-window-card" content-style="height: 100%">
          <ChatWindow :user="selectedUser" :messages="messages" :allUsers="users" @sendMessage="handleSendMessage" @receiveMessage="handleReceiveMessage" />
        </n-card>
      </n-layout>
    </n-layout>
  </div>
</template>

<script>
import UserList from '@/components/UserList.vue'
import ChatWindow from '@/components/ChatWindow.vue'
import { ref, onMounted } from 'vue'
import axios from '@/utils/axios'

export default {
  name: 'ChatView',
  components: { UserList, ChatWindow },
  setup() {
    const users = ref([])
    const selectedUser = ref(null)
    const messages = ref([])
    // 所有聊天记录缓存，key为userId
    const allMessages = ref({})

    // 获取联系人
    const fetchUsers = async () => {
      const res = await axios.get('/users')
      users.value = res
      if (res.length > 0) {
        selectedUser.value = res[0]
        fetchMessages(res[0].id)
      }
    }

    // 获取聊天记录
    const fetchMessages = async (userId) => {
      const res = await axios.get(`/chat-records/${userId}`)
      const msgArr = res.map(item => ({
        text: item.msg,
        self: item.type === 1,
        id: item.id
      }))
      messages.value = msgArr
      allMessages.value[userId] = msgArr
      localStorage.setItem('chat_' + userId, JSON.stringify(msgArr))
    }

    // 切换联系人
    const selectUser = (user) => {
      selectedUser.value = user
      // 优先加载本地
      const local = localStorage.getItem('chat_' + user.id)
      if (local) {
        messages.value = JSON.parse(local)
      } else {
        messages.value = []
      }
      // 也同步后端
      fetchMessages(user.id)
    }

    // 发送消息（交给ChatWindow处理WebSocket发送）
    const handleSendMessage = (msg) => {
      messages.value.push(msg)
      allMessages.value[selectedUser.value.id] = [...messages.value]
      localStorage.setItem('chat_' + selectedUser.value.id, JSON.stringify(messages.value))
    }

    // 收到WebSocket消息，分配到对应联系人
    const handleReceiveMessage = (msg) => {
      const userId = msg.userId
      if (!allMessages.value[userId]) {
        allMessages.value[userId] = []
      }
      allMessages.value[userId].push(msg)
      // 如果当前窗口就是这个联系人，追加到messages
      if (selectedUser.value && selectedUser.value.id === userId) {
        messages.value.push(msg)
      }
      localStorage.setItem('chat_' + userId, JSON.stringify(allMessages.value[userId]))
    }

    onMounted(() => {
      fetchUsers()
    })

    return { users, selectedUser, messages, selectUser, handleSendMessage, handleReceiveMessage }
  }
}
</script>

<style scoped>
.chat-container {
  height: 100vh;
}
.chat-window-card {
  height: 100%;
  min-height: 100vh;
  box-shadow: none;
  border-radius: 0;
}
</style> 