<template>
  <div class="chat-window-inner">
    <div class="chat-header">
      <n-avatar :size="40" round style="margin-right: 12px;">{{ user?.chatName?.charAt(0) || '' }}</n-avatar>
      <div class="user-info">
        <div class="user-name">{{ user?.chatName || '' }}</div>
        <div class="user-status">在线</div>
      </div>
    </div>
    <div class="chat-messages" ref="msgListRef">
      <div v-for="(msg, idx) in messages" :key="msg.id || idx" :class="['message-row', msg.self ? 'self' : 'other']">
        <n-avatar v-if="!msg.self" :size="32" round style="margin-right: 8px;">{{ user?.chatName?.charAt(0) || '' }}</n-avatar>
        <n-thing class="message-bubble" :class="msg.self ? 'bubble-self' : 'bubble-other'">
          <template #header>
            <span v-if="msg.self">我</span>
            <span v-else>{{ user?.chatName || '' }}</span>
          </template>
          <div>{{ msg.text }}</div>
        </n-thing>
        <n-avatar v-if="msg.self" :size="32" round style="margin-left: 8px; background: #18a058; color: #fff;">我</n-avatar>
      </div>
    </div>
    <div class="chat-input-bar">
      <n-input
        v-model:value="input"
        type="textarea"
        placeholder="输入消息..."
        :autosize="{ minRows: 1, maxRows: 4 }"
        @keydown.enter.exact.prevent="sendMessage"
        class="chat-input-box"
      />
      <n-button type="primary" @click="sendMessage" :disabled="!input.trim()" class="send-btn">发送</n-button>
    </div>
  </div>
</template>

<script>
import { ref, watch, nextTick, onMounted, onBeforeUnmount } from 'vue'
import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'
export default {
  name: 'ChatWindow',
  props: {
    user: Object,
    messages: Array,
    allUsers: Array // 新增，传入所有联系人
  },
  emits: ['sendMessage', 'receiveMessage'],
  setup(props, { emit }) {
    const input = ref('')
    const msgListRef = ref(null)
    let stompClient = null
    let subscriptions = {}

    // 订阅所有联系人频道
    const subscribeAllUsers = (userList) => {
      // 取消所有旧订阅
      Object.values(subscriptions).forEach(sub => sub.unsubscribe && sub.unsubscribe())
      subscriptions = {}
      if (stompClient && stompClient.connected && userList && userList.length) {
        userList.forEach(user => {
          if (user && user.id) {
            subscriptions[user.id] = stompClient.subscribe(`/topic/message/${user.id}`, (msg) => {
              const data = typeof msg.body === 'string' ? JSON.parse(msg.body) : msg.body
              emit('receiveMessage', {
                userId: user.id,
                text: data.msg,
                self: data.type === 1,
                id: data.id
              })
              // 如果当前窗口就是这个用户，自动滚动
              if (props.user && props.user.id === user.id) {
                nextTick(() => {
                  if (msgListRef.value) {
                    msgListRef.value.scrollTop = msgListRef.value.scrollHeight
                  }
                })
              }
            })
          }
        })
      }
    }

    // WebSocket连接
    const connectWebSocket = () => {
      stompClient = new Client({
        brokerURL: undefined,
        webSocketFactory: () => new SockJS('http://localhost:8081/ws'),
        reconnectDelay: 2000,
        onConnect: () => {
          if (props.allUsers) {
            subscribeAllUsers(props.allUsers)
          }
        }
      })
      stompClient.activate()
    }

    // 发送消息
    const sendMessage = () => {
      if (input.value.trim() && stompClient && stompClient.connected && props.user) {
        stompClient.publish({
          destination: '/app/message',
          body: JSON.stringify({
            msg: input.value,
            userId: props.user.id
          })
        })
        emit('sendMessage', {
          text: input.value,
          self: true,
          id: Date.now()
        })
        input.value = ''
        nextTick(() => {
          if (msgListRef.value) {
            msgListRef.value.scrollTop = msgListRef.value.scrollHeight
          }
        })
      }
    }

    onMounted(() => {
      connectWebSocket()
    })
    onBeforeUnmount(() => {
      if (stompClient) stompClient.deactivate()
      Object.values(subscriptions).forEach(sub => sub.unsubscribe && sub.unsubscribe())
    })

    // 联系人变化时重新订阅
    watch(() => props.allUsers, (newUsers) => {
      if (stompClient && stompClient.connected && newUsers) {
        subscribeAllUsers(newUsers)
      }
    }, { immediate: true })

    // 切换联系人或消息变化时自动滚动
    watch(() => props.user && props.user.id, () => {
      nextTick(() => {
        if (msgListRef.value) {
          msgListRef.value.scrollTop = msgListRef.value.scrollHeight
        }
      })
    })
    watch(() => props.messages, () => {
      nextTick(() => {
        if (msgListRef.value) {
          msgListRef.value.scrollTop = msgListRef.value.scrollHeight
        }
      })
    })

    return { input, sendMessage, msgListRef }
  }
}
</script>

<style scoped>
.chat-window-inner {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #f7f9fb;
}
.chat-header {
  display: flex;
  align-items: center;
  padding: 18px 24px;
  background: #fff;
  border-bottom: 1px solid #f0f0f0;
  box-shadow: 0 2px 8px #f0f1f2;
}
.user-info {
  display: flex;
  flex-direction: column;
}
.user-name {
  font-weight: bold;
  font-size: 18px;
}
.user-status {
  font-size: 12px;
  color: #18a058;
}
.chat-messages {
  flex: 1;
  padding: 24px 24px 12px 24px;
  overflow-y: auto;
  background: #f7f9fb;
}
.message-row {
  display: flex;
  align-items: flex-end;
  margin-bottom: 16px;
}
.message-row.self {
  flex-direction: row-reverse;
}
.message-bubble {
  max-width: 60%;
  padding: 10px 16px;
  border-radius: 18px;
  font-size: 15px;
  word-break: break-all;
  box-shadow: 0 2px 8px #e0eaff33;
}
.bubble-self {
  background: linear-gradient(135deg, #18a058 0%, #36ad6a 100%);
  color: #fff;
  margin-left: 8px;
}
.bubble-other {
  background: #fff;
  color: #333;
  margin-right: 8px;
  border: 1px solid #e0eaff;
}
.chat-input-bar {
  display: flex;
  align-items: flex-end;
  padding: 18px 24px;
  background: #fff;
  border-top: 1px solid #f0f0f0;
}
.chat-input-box {
  flex: 1;
  margin-right: 12px;
}
.send-btn {
  min-width: 80px;
}
</style> 